.btn_back {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 0;
  padding: 8px 16px;
  border-radius: 8px;
  background-color: #81cbad;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  display: inline-block;
  color: black;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  min-width: 180px;
  max-width: 300px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.btn_back:hover {
  background-color: #19724f;
  color: #fff;
}
